@import 'utils/variables', 'utils/mixins';

.wrapper {
  width: 650px;
  max-height: 60vh;

  z-index: 15;

  padding: 20px 0;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;

  font-weight: 300;

  background-color: $white;
  border-radius: 8px;

  box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.15);

  @include darkTheme {
    background: $black-blue;
  }
}

.loader {
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}

.header {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  padding: 0 20px;
}

.headerLabel {
  font-size: $header;
  line-height: 24px;
  color: $base-black;
  font-weight: 700;

  @include darkTheme {
    color: $grey;
  }
}

.close {
  cursor: pointer;
  width: 13px;
  height: 13px;

  margin-bottom: -2px;
}

.search {
  width: 300px;
  height: 45px;
}

.list {
  list-style: none;
  width: 100%;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;

  overflow-y: auto;
}

.item {
  width: 100%;
  border-top: 0.5px solid $light-blue;
  border-bottom: 0.5px solid $light-blue;
}

.loader {
  margin-top: 20px;

  opacity: 0.5;

  display: flex;
  align-items: center;
  justify-content: center;
}

.emptyContainer {
  width: 100%;
  height: 50px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.emptyText {
  font-size: 13px;
  line-height: 16px;
  color: #565656;

  @include darkTheme {
    color: $white;
  }
}

.error {
  width: 100%;
  height: 50px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.errorText {
  font-size: 13px;
  line-height: 16px;
  color: #565656;

  @include darkTheme {
    color: $white;
  }
}

.actions {
  margin-top: 30px;
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.action {
  width: 165px;
  height: 45px;

  @include respond-to('medium') {
    width: 160px;
    height: 30px;
  }
}

.action span {
  font-size: 14px;
  line-height: 24px;

  @include respond-to('medium') {
    font-size: 12px;
  }
}

.action button {
  width: 100%;
  height: 100%;
}

.separateLine {
  border-bottom: 1px solid $base-gray;
  margin: 15px -15px 25px -15px;
  width: 111%;
}

.container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 20px;
}

.button {
  width: 135px;
  height: 35px;

  font-size: 12px;
  line-height: 16px;

  border-radius: 3px;

  @include respond-to('large') {
    width: 130px;
    height: 25px;
    font-size: 10px;
  }

  @include respond-to('medium') {
    width: 105px;
    height: 20px;
    font-size: 9px;
  }

  &.apply {
    background-color: $dark-blue;

    @include darkTheme {
      background-color: $moderate-blue;
      color: $jet;
      border: $moderate-blue;
    }

    &:disabled {
      background-color: $blue-button;
      color: $white;
      border: $blue-button;

      @include darkTheme {
        background-color: $grey;
        color: $jet;
        border: $grey;
      }
    }
  }
}

.label {
  padding: 0 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  font-size: 14px;
  margin-bottom: 10px;
}